@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  padding: 5% 0%;
  font-family: "Press Start 2P", cursive;
  font-size: x-small;
  background: linear-gradient(to left, rgb(7, 1, 34) 0%, rgb(17, 42, 44) 100%);
}

.tell-me {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  width: 350px;
  height: auto;
}
.btn-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 10px;
}
button {
  border-radius: 35px;
}

button:hover {
  transform: scale(1.1);
}

button:active {
  transform: translate(0.2rem, 0.2rem);
}

p {
  padding: 5px 5px;
  color: rgb(6, 0, 37);
}

